@import '../../utils/styles/colors.scss';


//Dashboard
.Container-dashbord{
  display: flex;
  flex-direction: column;
  margin-top: 150px;
  position: fixed;
  width: 20%;
  margin-left: 100px;
  @media (max-width: 1080px) {
    flex-direction: row;
    justify-content: flex-start;
    z-index: 10;
    width: auto;
    background: $rouge;
    margin-left: 0px;
    margin-top: 0px;
    position: sticky;
    top: 0;
    height: 70px;
    box-shadow: 0px 5px 8px -2px rgba(0, 0, 0, 0.51);
    -webkit-box-shadow: 0px 5px 8px -2px rgba(0, 0, 0, 0.51);
    -moz-box-shadow: 0px 5px 8px -2px rgba(0, 0, 0, 0.51);
  }
}


//Profil
.Container-profil{
  display: flex;
  justify-content: flex-start;
  margin-top: 20px;
  width: 246px;
  @media (max-width: 1080px) {
   width: auto;
   background: $rouge;
   height: 50px;
   align-items: center;
   margin-top: 20px;
  }
}

.Container-profil p {
  margin: 18px 10px 0px 0px;
  font-size: 18px;
  font-weight: 500;
  line-height: 26px;
  letter-spacing: 0em;
  text-align: right;
  width: 190px;
  height: 52px;
  color: $rouge;
  @media (max-width: 1080px) {
   display: none;
  }
}

.Navbar-responsiv{
 @media (max-width: 1080px) {
  display: flex;
  flex-direction: row;
    position: sticky;
    margin-top: 30px;
    padding-left: 20px;
    width: 280px;
    gap: 8px;
   }
}

.Container-profil img {
  width: 64px;
  height: 64px;
  background: $noir;
  border-radius: 30px;
  @media (max-width: 1080px) {
   margin-left: 20px;
   margin-top: 40px;
   }
}

//Filtre
.Container-filtre-navbar{
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  @media (max-width: 1080px) {
    width: 0%;
   }
}

.Container-filtre{
  @media (max-width: 1080px) {
    display: none;
   }
}

h2{
  font-size: 16px;
  color: $blanc;
}

ul{
  list-style: none;
  margin-top: 5px;
  @media (max-width: 1080px) {
    width: 90px;
   }
}

li{
  margin-top: 10px;
  @media (max-width: 1080px) {
    display: flex;
    flex-direction: row;
    margin-right: 15px;
   }
}

a{
  color: $blanc;
  font-size: 13px;
  cursor: pointer;
  text-decoration: none;
  @media (max-width: 1080px) {
    color: $blanc;
    font-weight: bold;
   }
}

a:hover{
  color: $rouge;
  @media (max-width: 1080px) {
    color: $blanc;
   }
}

.active{
  color: $rouge;
  @media (max-width: 1080px) {
    color: $noir;
   }
}

.Border{
  width: 200px;
  height: 5px;
  border-top: 1px solid $blanc;
  margin-top: 20px;
}

//Navbar

svg{
  margin-right: 10px;
}

.fa-house{
  color: $rouge;
  @media (max-width: 1080px) {
    color: $noir;
   }
}

.fa-book, .fa-user-group{
  color: $blanc;
  margin-right: 14px;
  @media (max-width: 1080px) {
    margin-right: 10px;
   }
}

.Amis {
  display: none;
  @media (max-width: 1080px) {
    display: block;
   }
}

.Amis li{
  @media (max-width: 1080px) {
    width: 30px;
   }
}
