@import "../../utils/styles/colors.scss";

footer {
  display: flex;
  position: fixed;
  width: 30%;
  top: 93%;
  left: 25%;
  height: 500px;
  @media (max-width: 1080px) {
    flex-direction: column;
    justify-content: center;
    position: relative;
    width: auto;
    height: 100px;
    flex-wrap: wrap;
    left: 0;
  }
}

footer div {
  display: flex;
  flex-direction: column;
  @media (max-width: 1080px) {
    justify-content: center;
    align-items: center;
    width: 100%;
  }
}

.justelire p {
  font-size: 8px;
  color: $blanc;
  margin-left: 20px;
  @media (max-width: 1080px) {
    display: flex;
    text-align: center;
    justify-content: center;
    width: 190px;
    height: 30px;
    margin: 0px;
  }
}

footer a {
  color: $rouge;
  margin-left: 20px;
  text-decoration: none;
  @media (max-width: 1080px) {
    margin: 0px;
    margin-bottom: 10px;
  }
}

footer a:hover {
  color: $rouge-hover;
}

.justelire {
  display: flex;
  flex-direction: row;
  @media (max-width: 1080px) {
    width: 350px;
    height: auto;
    flex-direction: column;
  }
}
